:root {
  --second-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.logo_link {
  display: inline-block;
}

.logo_moyo {
  width: 100%;
  height: 100%;
  display: block;
}

.nav {
  margin-right: 50px;
  display: inline-block;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.nav_item :hover, .nav__link:hover {
  color: var(--second-color);
  -webkit-text-decoration: none;
  text-decoration: none;
  text-decoration-thickness: 2px;
}

.is-active {
  color: var(--second-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--second-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

